@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

:root {
  --point_color: #997dff;
  color: goldenrod
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  color: white;
  font-family: 'Pretendard', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  text-decoration: none;
  background: 0;
}

html {
  background: #000;
}

#wrap {
  padding: 83px 5% 5% 5%;
}

a:link {
  color: #000;
}

h1 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  padding: 30px 5%;
  font-size: 22px;
  font-weight: 800;
}

h1::after {
  content: '';
  z-index: -1;
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  height: 92px;
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
  filter: blur(10px);
}


h1 b {
  font: inherit;
  color: var(--point_color);
}

.movie_list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  row-gap: 30px;
  padding-top: 30px;
}