.movie {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: calc(25% - calc(30px / 4));
}

@media screen and (max-width: 799px) {
  .movie {
    width: calc(33.3333% - 10px);
  }
}

@media screen and (max-width: 599px) {
  .movie {
    width: calc(50% - 5px);
  }
}

.movie a>img {
  width: 100%;
  margin-bottom: 10px;
  border-radius: 10px;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.2);
}

.movie a h2 {
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  min-height: 50.3px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: calc(18px * -0.2);
}

.movie b {
  color: var(--point_color);
  font-weight: 600;
}

.movie ul {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.movie ul li {
  padding: 3px 10px 4px;
  border-radius: 9.5px;
  color: #333;
  font-size: 12px;
  white-space: nowrap;
  background: #ddd;
}

.movie p {
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 14px;
  line-height: 1.2;
}

.background {
  display: none;
  position: fixed;
  top: 50%;
  left: -3%;
  right: -3%;
  z-index: -1;
  transform: translateY(-50%);
  opacity: 0.4;
  filter: blur(5px);
}

.background img {
  width: 100%;
}

.show {
  display: flex;
}